@import '../../globals.scss';
.leftbar {
  @include themify($themes){
    flex:2;
    position: sticky;
    top:60px;
    height: calc(100vh - 60px);
    overflow:scroll;
    background-color: themed("bg");
    color:themed("textColor");
    &::-webkit-scrollbar{
      display: none;
    }

    @include mobile{
      display:none;
   }

  .container {
    padding: 16px;
    hr {
        margin: 16px 0px;
        border: none;
        height:0.5px;
        background-color:themed("border");;
    }
    .menu {
      display: flex;
      flex-direction: column;
      gap: 20px;
      span{
          font-size: 12px;
      }
      .user {
        display: flex;
        align-items: center;
        gap: 10px;
        img {
          width: 30px;
          height: 30px;
          border-radius: 50%;
          object-fit: cover;
        }
        span {
            font-size:14px;
        }
      }
      .item {
        display: flex;
        align-items: center;
        gap: 10px;
        img {
          width: 30px;
          height: 30px;
        }
        span {
            font-size:14px;
        }

      }
    }
  }
}
}
